import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'home',
    pathMatch: 'full'
  },
  {
    path: 'home',
    loadChildren: () => import('./pages/home/home.module').then( m => m.HomePageModule)
  },
  {
    path: 'login',
    loadChildren: () => import('./pages/login/login.module').then( m => m.LoginPageModule)
  },
  {
    path: 'product-orders',
    loadChildren: () => import('./pages/product-orders/product-orders.module').then( m => m.ProductOrdersPageModule)
  },
  {
    path: 'product-order-detail',
    loadChildren: () => import('./pages/product-order-detail/product-order-detail.module').then( m => m.ProductOrderDetailPageModule)
  },
  {
    path: 'product-order-detail/:id',
    loadChildren: () => import('./pages/product-order-detail/product-order-detail.module').then( m => m.ProductOrderDetailPageModule)
  },
  {
    path: 'products-list',
    loadChildren: () => import('./pages/products-list/products-list.module').then( m => m.ProductsListPageModule)
  },
  {
    path: 'product-detail/:id',
    loadChildren: () => import('./pages/product-detail/product-detail.module').then( m => m.ProductDetailPageModule)
  },
  {
    path: 'product-detail',
    loadChildren: () => import('./pages/product-detail/product-detail.module').then( m => m.ProductDetailPageModule)
  },
  {
    path: 'configuration',
    loadChildren: () => import('./pages/configuration/configuration.module').then( m => m.ConfigurationPageModule)
  },
  {
    path: 'users',
    loadChildren: () => import('./pages/users/users.module').then( m => m.UsersPageModule)
  },
  {
    path: 'audit-trail',
    loadChildren: () => import('./pages/audit-trail/audit-trail.module').then( m => m.AuditTrailPageModule)
  },
  {
    path: 'tools',
    loadChildren: () => import('./pages/tools/tools.module').then( m => m.ToolsPageModule)
  },
  {
    path: 'user-detail/:id',
    loadChildren: () => import('./pages/user-detail/user-detail.module').then( m => m.UserDetailPageModule)
  },
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule {}
